import React from 'react';
import {Chart} from 'primereact/chart';
import '../App.css';

export default class DonutChart extends React.Component {
    
    render() {
        const data = {
            labels: ['A','B','C'],
            datasets: [
                {
                    data: [1, 1, 5],
                    backgroundColor: [
                        "#FF6384",
                        "#36A2EB",
                        "#FFCE56",
                    ],
                    hoverBackgroundColor: [
                        "#FF6384",
                        "#36A2EB",
                        "#FFCE56"
                    ]
                }]    
            };
        
            return (
                <div className="data2">
                    <Chart type="doughnut" data={data} />
                </div>
            );
    }
}