import React from 'react';
import {Chart} from 'primereact/chart';
import '../App.css';

export default class LineChart extends React.Component {
    render() {
        const data_line = {
            labels: [0, 1, 2, 3, 4, 5],
            datasets: [
                {
                    label: 'First Dataset',
                    data: [0, 200, 100, 400, 250],
                    fill: false,
                    backgroundColor: '#42A5F5',
                    borderColor: '#42A5F5'
                }
            ]   
        };

        return (
            <div className="data3">
                <Chart type="line" data={data_line} />
            </div>
        );
    }
}