import React from 'react';
import {DataTable} from 'primereact/datatable';
import {Column} from 'primereact/column';
import table from  './table';
import '../App.css';

export default class TableData extends React.Component {
    constructor() {
        super();
        this.state = {
            jsonData: table.data
        };
    }

    render() {
        return (
            <div className="container">
                <div className="data1">
                    <DataTable value={this.state.jsonData} style={{width: '350px'}} paginator={true} rows={10} rowsPerPageOptions={[5,10,20]} sortMode="multiple">                        
                        <Column field="client_name" header="Client Name" sortable={true}/>
                        <Column field="n_amount" header="Notional Amount" sortable={true}/>
                    </DataTable>
                </div>
            </div>
        );
    }

    onCityChange(e) {
        this.setState({city: e.value});
    }
}